.Home,
.NotFound {
  padding-top: 0 !important; }
  .Home-header,
  .NotFound-header {
    display: flex;
    justify-content: center;
    height: 100vh;
    background: url(../../../imagens/background.jpg) no-repeat center 0px; }
    .Home-header .LogoStarWars,
    .Home-header .imgNotFound,
    .NotFound-header .LogoStarWars,
    .NotFound-header .imgNotFound {
      align-self: center; }
      .Home-header .LogoStarWars img,
      .Home-header .imgNotFound img,
      .NotFound-header .LogoStarWars img,
      .NotFound-header .imgNotFound img {
        width: 600px; }
        @media screen and (max-width: 480px) {
          .Home-header .LogoStarWars img,
          .Home-header .imgNotFound img,
          .NotFound-header .LogoStarWars img,
          .NotFound-header .imgNotFound img {
            width: 300px; } }



